import Button from "@material-ui/core/Button";
import Container from "@material-ui/core/Container";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";

//import ProTip from "../components/ProTip";
import RemoteControl from "../components/RemoteControl";

import AirConditioner from "../features/ac/AirConditioner";
import Toast from "../features/toast/Toast";

import { ThemeProvider } from "../theme";

import { useAppSelector } from "../app/hooks";
import { RootState } from "../app/store";
import Copyright from "../layouts/Copyright";
import { useDetectStorage } from "../features/ac";
import React from "react";

function Home() {
  const ac = useAppSelector((state: RootState) => state.ac);

  useDetectStorage();

  /**
   * 根据模式返回对应的色温
   * @returns
   */
  function getClassByMode() {
    if (ac.status) {
      return ac.mode === "hot" ? "hot-color" : "cold-color";
    } else {
      return "";
    }
  }

  function Ad() {
    return (
        // <Box align="center" mt={12}>
        <Box textAlign="center" marginBottom={-2}  mt={4}>
          <div className="text-center">
            <Box sx={{ mt: 2 }}>
              <Button
                  variant="outlined"
                  onClick={() => {
                    window.location.href="http://dpurl.cn/glwViGXz";
                  }}
              >
                领个红包点外卖，喝阔乐吹空调，美滋滋~
              </Button>
            </Box>
          </div>
        </Box>
    );
  }

  return (
    <ThemeProvider>
      <Container maxWidth="sm" className={getClassByMode()}>
        <Box sx={{ pt: 4 }} bgcolor="transparent">
          <Typography
            color="textPrimary"
            align="center"
            variant="h6"
            component="h1"
            gutterBottom
          >
            蜡笔小新牌小空调
          </Typography>

          <AirConditioner
            status={ac.status}
            temperature={ac.temperature}
            mode={ac.mode}
          />
          <div className="text-center">
            <Box sx={{ mt: 2 }}>
              <Button
                variant="outlined"
                onClick={() => {
                  window.open("#/rc", "_blank", "width=300, height=400");
                }}
              >
                独立遥控器
              </Button>
            </Box>
          </div>
          <RemoteControl />
          <Ad />
        </Box>
        <Toast />
      </Container>
    </ThemeProvider>
  );
}

export default Home;
